* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* body{
   
} */
section {
  background-color: #191b1c;
  width: 80%;
  height: 100%;
  margin: auto;
  margin-top: 2rem;
  border-radius: 1rem;
}
section .live {
  width: 100%;
  height: 2.8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border: 2px solid red; */

}
.live .right {
  width: 50%;
  color: rgba(0, 189, 215, 1);
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.live .right .anim {
  animation: live 1s infinite alternate;
}
@keyframes live {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

.live .left {

  width: 50%;
  height: inherit;
  /* border: 2px solid red; */
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.live .right h2 {
  color: rgba(0, 189, 215, 1);
  margin-right: 1rem;
  margin-left: 5px;
  font-size: 1rem;
}
.hero {
  height: 100%;
  width: 100%;
  display: grid;
  padding: 16px;
  grid-gap: 16px;
  justify-content: center;
  align-items: center;
}
.hero .s1 {
  height: 8rem;
  width: 15rem;
  border-radius: 8px;
  background-color: #1e2124;
  margin-bottom: 16px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 700px) {
  .hero {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 700px) {
  .hero {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 700px) {
  .hero {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 700px){
  .live .left {
    width: 80%;
    height: inherit;
    }
}

.hero .s1 p {
  color: white;
  font-style: normal;
  text-align: center;
}
.hero .s1 h3 {
  color: rgba(0, 189, 215, 1);
  text-align: center;
  margin-top: 1rem;
  font-size: 1.5rem;
}
marquee {
  color: white;
  background-color: #1e2124;
  padding: 5px;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

footer {
  text-align: center;
  align-items: center;
  margin-top: 10px;
  height: 4rem;
  width: 100vw;
  background-color: #1e2122;
}
footer p {
  color: rgba(255, 255, 255, 0.5);
  position: relative;
  top: 1rem;
}
footer p span {
  color: rgba(0, 189, 215, 1);
}
footer p a {
  color: rgba(255, 255, 255, 0.5);
}
